html,
body,
#root {
  min-height: 100vh;
  margin: 0;
}

.App {
  text-align: center;
  min-height: 100vh;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  max-width: 1920px;
  margin: 0 auto;
}
